.project-container{
    max-width: 1140px;
    margin: 5rem;
    display: grid;
    margin-top: 30px;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;  
}

.img-fluid{
    height: 200px;
    width: 100%;
}
.bg-image, .card-body{
    background-color: gainsboro;
}

.card-title, .btn{
    color: black;
}

.btn:hover{
 background-color: ghostwhite;
 color: black;
}

@media screen and (max-width:1040px) {
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
    .card{
        height: 400px;
    }
    
}